import React from 'react';
import { makeStyles, Grid, TextField, Button, CircularProgress } from '@material-ui/core';
import Typography from '../components/Typography';
import { Formik } from 'formik';
import FlexBox from '../components/FlexBox';
import contactPageImage1 from '../media/contact/contactPage1.png';
import { ReactComponent as LongArrowRight } from '../media/longArrowRight.svg';
import PageTitle from '../components/PageTitle';
import { initialContactForm, validateContactForm } from '../models/ContactForm';
import { postContactForm } from '../api/googleSheetsApiClient';
import { anyIsError, anyIsIdle, anyIsPending, anyIsSuccess, useFetchedResource } from '../utils/fetchers';

const useStyles = makeStyles((theme) => ({
  image: {
    width: '100%',
  },
  formContainer: {
    background: '#222223',
    zIndex: 1,
    padding: theme.spacing(3),
    maxWidth: 580,
  },
  contactContentContainer: {
    position: 'relative',
    background: `url(${contactPageImage1}) no-repeat center center / cover`,
    padding: '115px 15px',
    width: '100%',
    justifyContent: 'center',
  },
  textInput: {
    '& *': {
      color: 'white',
    },
    '& .MuiInput-underline:before': {
      borderColor: 'white',
    },
    '& .MuiInput-underline:hover:not(.Mui-disabled):before': {
      borderColor: 'white',
    },
    '& label': {
      fontFamily: 'AngelicBonques',
      fontWeight: 'bold',
    },
  },
  boldWhiteText: {
    fontWeight: 600,
    color: 'white',
  },
  arrowSvg: {
    fill: 'white',
    height: '1em',
    marginLeft: theme.spacing(3),
  },
  fullWidthCentered: {
    width: '100%',
    display: 'flex',
    justifyContent: 'center',
  },
}));

const ContactPage = () => {
  const classes = useStyles();
  const [response, submitContactForm] = useFetchedResource(postContactForm);

  return (
    <FlexBox className={classes.contactContentContainer} >
      {/* <img src={contactPageImage1} className={classes.image} alt='Workdesk with coffee and laptop' /> */}
      <Formik
        initialValues={initialContactForm}
        validate={validateContactForm}
        onSubmit={submitContactForm}
      >
        {(props) => (
          <FlexBox flexDirection='column' className={classes.formContainer}>
            <PageTitle className={classes.boldWhiteText}>
              Contact
            </PageTitle>
            <Grid container spacing={4}>
              <Grid item xs={12}>
                <TextField className={classes.textInput} fullWidth label='Name' value={props.values.name} onBlur={props.handleBlur} onChange={props.handleChange} name='name' />
              </Grid>
              <Grid item xs={12} sm={6}>
                <TextField className={classes.textInput} fullWidth required label='Email' value={props.values.email} onBlur={props.handleBlur} onChange={props.handleChange} name='email' type='email' error={!!(props.touched.email && props.errors.email)} helperText={(props.touched.email && props.errors.email) || ' '} />
              </Grid>
              <Grid item xs={12} sm={6}>
                <TextField className={classes.textInput} fullWidth label='Phone' value={props.values.phone} onBlur={props.handleBlur} onChange={props.handleChange} name='phone' type='tel' />
              </Grid>
              <Grid item xs={12}>
                <TextField className={classes.textInput} fullWidth multiline label='Message' value={props.values.message} onBlur={props.handleBlur} onChange={props.handleChange} name='message' />
              </Grid>
              <Grid item xs={12} >
                {anyIsIdle(response) ?
                  <Button fullWidth onClick={props.submitForm}>
                    <Typography header variant='h5' className={classes.boldWhiteText}>
                      Send
                    </Typography>
                    <LongArrowRight className={classes.arrowSvg} />
                  </Button> :
                  <div className={classes.fullWidthCentered}>
                    {anyIsPending(response) ?
                      <CircularProgress /> :
                      anyIsSuccess(response) ?
                        <Typography header variant='h5' className={classes.boldWhiteText}>Sent!</Typography> :
                        anyIsError(response) ?
                          <Typography color='error' header>Our apologies. Something went wrong. Please try again.</Typography> : null}
                  </div>

                }
              </Grid>
              <Grid item xs={12} >
                <FlexBox justifyContent='center'>
                  <Typography header variant='h5' className={classes.boldWhiteText}>
                    Initial Consultation Is Complementary
                  </Typography>
                </FlexBox>
              </Grid>
            </Grid>
          </FlexBox>
        )}
      </Formik>
    </FlexBox>
  );
};

export default ContactPage;