import React from 'react';
import { Card, CardActionArea, CardMedia, CardMediaProps } from '@material-ui/core';

export type ImageCardProps = CardMediaProps & { onClick?: ((event: React.MouseEvent<HTMLButtonElement, MouseEvent>) => void), buttonClass?: string, delay?: number };
const ImageCard = ({ image, title, children, className, onClick, buttonClass, delay = 0 }: ImageCardProps) => {
  return (
    <Card elevation={2}>
      <CardActionArea onClick={onClick} className={buttonClass}>
        <CardMedia
          data-aos-delay={delay}
          component="img"
          className={className}
          image={image}
          title={title}
        />
        {children}
      </CardActionArea>
    </Card>
  );
};

export default ImageCard;