/**
 * Import all images with a require context, and return them as a list of strings.
 * @param r - A call to `require.context('string-literal-path-to-image-directory', isNested, regexPattern)
 * NOTE: All the arguments to require.context need to be literals.  A good regex for images is /\.(png|jpe?g|svg)$/
 * @returns string[] - App src paths to all the images.
 */
export const importAllImages = (r: __WebpackModuleApi.RequireContext) => {
  return r.keys().map(r) as string[];
};