import { makeStyles } from '@material-ui/core';
import React from 'react';
import classNames from 'classnames';
import { SLIDE_DURATION_MS, TEXT_SLIDE_EXTRA_MS } from './BannerStepper';

const useStyles = makeStyles((theme) => ({
  arrow: {
    position: 'relative',
    left: '50%',
    marginLeft: '-20px',
    width: 40,
    height: 40,

    /**
     * Dark Arrow Down
     */
    backgroundImage: 'url(data:image/svg+xml;base64,)',
    backgroundSize: 'contain',
  },
  bounce: {
    animation: `$bounce 2s ${(SLIDE_DURATION_MS + TEXT_SLIDE_EXTRA_MS) / 1000 + 0.5}s`,
  },
  '@keyframes bounce': {
    '0%, 20%, 50%, 80%, 100%': {
      transform: 'translateY(0)',
    },
    '40%': {
      transform: 'translateY(-30px)',
    },
    '60%': {
      transform: 'translateY(-15px)',
    },
  },
}));

const ArrowBouncer = () => {
  const classes = useStyles();
  return (
    <div className={classNames(classes.arrow, classes.bounce)}></div>
  );
};

export default ArrowBouncer;