import React from 'react';
import { makeStyles, useTheme } from '@material-ui/core';
import MobileStepper from '@material-ui/core/MobileStepper';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import KeyboardArrowLeft from '@material-ui/icons/KeyboardArrowLeft';
import KeyboardArrowRight from '@material-ui/icons/KeyboardArrowRight';
import SwipeableViews from 'react-swipeable-views';
import { autoPlay } from 'react-swipeable-views-utils';

const AutoPlaySwipeableViews = autoPlay(SwipeableViews);

const useStyles = makeStyles((theme) => ({
  root: {
    // flexGrow: 1,
  },
  header: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between',
    height: 50,
    paddingLeft: theme.spacing(4),
    backgroundColor: theme.palette.background.default,
  },
  imgContainer: {
    display: 'flex',
    justifyContent: 'center',
  },
  img: {
    // width: '100%',
    maxWidth: '100%',
    maxHeight: `calc(100vh - ${theme.spacing(8) + 100}px)`,
    objectFit: 'contain',
  },
}));

type SwipeableTextMobileStepperProps = {
  steps: { image: string, label?: string }[],
  start?: number,
  closeButton?: React.ReactNode,
};
function SwipeableTextMobileStepper({ steps, start = 0, closeButton }: SwipeableTextMobileStepperProps) {
  const classes = useStyles();
  const theme = useTheme();
  const [activeStep, setActiveStep] = React.useState(start);
  const maxSteps = steps.length;

  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleStepChange = (step: number) => {
    setActiveStep(step);
  };

  return (
    <div className={classes.root}>
      <Paper square elevation={0} className={classes.header}>
        <Typography>{steps[activeStep].label}</Typography>
        {closeButton}
      </Paper>
      <AutoPlaySwipeableViews
        axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
        index={activeStep}
        onChangeIndex={handleStepChange}
        enableMouseEvents
        autoplay={false}
      >
        {steps.map(({ image, label }, index) => (
          <div key={`${label}-${index}`} className={classes.imgContainer}>
            {Math.abs(activeStep - index) <= 2 ? (
              <img className={classes.img} src={image} alt={label} />
            ) : null}
          </div>
        ))}
      </AutoPlaySwipeableViews>
      {maxSteps > 1 && <MobileStepper
        steps={maxSteps}
        position="static"
        variant="dots"
        activeStep={activeStep}
        nextButton={
          <Button size="small" onClick={handleNext} disabled={activeStep === maxSteps - 1}>
            Next
            {theme.direction === 'rtl' ? <KeyboardArrowLeft /> : <KeyboardArrowRight />}
          </Button>
        }
        backButton={
          <Button size="small" onClick={handleBack} disabled={activeStep === 0}>
            {theme.direction === 'rtl' ? <KeyboardArrowRight /> : <KeyboardArrowLeft />}
            Back
          </Button>
        }
      />}
    </div>
  );
}

export default SwipeableTextMobileStepper;
