import { makeStyles } from '@material-ui/core';
import { ChevronLeft, ChevronRight } from '@material-ui/icons';
import classNames from 'classnames';
import React, { useCallback, useEffect, useState } from 'react';
import { mod } from '../utils/base';
import FlexBox from './FlexBox';

export const TRANSITION_TIME_MS = 700;
export const SLIDE_DURATION_MS = TRANSITION_TIME_MS + 4000;
export const TEXT_SLIDE_EXTRA_MS = 500;

const useStyles = makeStyles((theme) => ({
  contentContainer: {
    position: 'relative',
  },
  image: {
    position: 'absolute',
    top: 0,
    left: 0,
    width: '100vw',
    transition: `all ${TRANSITION_TIME_MS / 1000}s ease-in`,
    opacity: 0,
  },
  leftButton: {
    position: 'absolute',
    top: 0,
    left: 0,
    width: '50vw',
    height: '100%',
    cursor: 'pointer',
    display: 'flex',
    justifyContent: 'flex-start',
    '&:hover > $chevron': {
      opacity: 1,
    }
  },
  rightButton: {
    position: 'absolute',
    top: 0,
    right: 0,
    width: '50vw',
    height: '100%',
    cursor: 'pointer',
    display: 'flex',
    justifyContent: 'flex-end',
    '&:hover > $chevron': {
      opacity: 1,
    }
  },
  chevron: {
    opacity: 0,
    background: 'rgba(0 0 0 / 50%)',
    height: '100%',
    display: 'flex',
    alignItems: 'center',
    transition: 'opacity .2s ease-in',
    '& > svg': {
      fill: 'white',
      height: '4rem',
      width: '4rem',
    },
  },
  activeImage: {
    position: 'relative',
    opacity: 1,
  },
  stepperDotsContainer: {
    padding: 8,
    background: 'white',
    alignItems: 'center',
    justifyContent: 'space-between',
    maxWidth: 400,
    flexGrow: 1,
  },
  stepperDot: {
    width: 8,
    height: 8,
    margin: '0 4px',
    borderRadius: '50%',
    backgroundColor: 'rgba(0, 0, 0, 0.26)',
  },
  activeStepperDot: {
    backgroundColor: theme.palette.primary.main,
  },
  pointer: {
    cursor: 'pointer'
  }
}));

type StepperDotsProps = {
  numDots: number,
  activeDot: number,
  onDotClick?: ((event: React.MouseEvent<HTMLDivElement, MouseEvent>, dotIdx: number) => void),
};
const StepperDots = ({ numDots, activeDot, onDotClick }: StepperDotsProps) => {
  const classes = useStyles();
  return (
    <FlexBox className={classes.stepperDotsContainer}>
      {Array(numDots).fill(1).map((v, i) => (
        <div key={i} className={classNames(classes.stepperDot, { [classes.activeStepperDot]: i === activeDot, [classes.pointer]: !!onDotClick })} onClick={(evt) => onDotClick && onDotClick(evt, i)}></div>
      ))}

    </FlexBox>
  );
}

type BlurStepperProps = { steps: { image: string, label?: string }[] };
const BannerStepper = ({ steps }: BlurStepperProps) => {
  const classes = useStyles();
  const [activeIdx, setActiveIdx] = useState(0);
  const [isTransitioning, setIsTransitioning] = useState(false);
  const [autoPlayId, setAutoPlayId] = useState<NodeJS.Timeout | null>(null);

  const handleClick = (incr: number) => () => {
    setActiveIdx((prevIdx) => mod(prevIdx + incr, steps.length));
    setIsTransitioning(true);
    autoPlayId && clearTimeout(autoPlayId);
    setTimeout(() => setIsTransitioning(false), TRANSITION_TIME_MS / 2);
  }

  const incrementImage = useCallback(handleClick(1), []);
  useEffect(() => {
    const id = setTimeout(() => {
      !isTransitioning && incrementImage();
    }, activeIdx === 0 ? SLIDE_DURATION_MS + TEXT_SLIDE_EXTRA_MS : SLIDE_DURATION_MS);
    setAutoPlayId(id);
    return () => clearTimeout(id);
  }, [isTransitioning, incrementImage, activeIdx]);

  return (
    <FlexBox flexDirection='column' alignItems='center'>
      <div className={classes.contentContainer}>
        {Array(3).fill(1).map((v, idx) => (
          <img key={steps[mod(activeIdx - 1 + idx, steps.length)].image} src={steps[mod(activeIdx - 1 + idx, steps.length)].image} className={classNames(classes.image, { [classes.activeImage]: idx === 1 })} alt={steps[activeIdx].label} />
        ))}
        <div onClick={handleClick(-1)} className={classes.leftButton}>
          <div className={classes.chevron}><ChevronLeft /></div>
        </div>
        <div onClick={handleClick(1)} className={classes.rightButton}>
          <div className={classes.chevron}><ChevronRight /></div>
        </div>
      </div>
      <StepperDots
        numDots={steps.length}
        activeDot={activeIdx}
        onDotClick={(evt, i) => {
          console.log(i, activeIdx);
          i !== activeIdx && handleClick(i - activeIdx)()
        }}
      />
    </FlexBox>
  );
};

export default BannerStepper;